@use "../global" as global;

body, main {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background: #000;
}
main {
  .token-body {
    position: relative;
    display: flex;
    flex-shrink: 0;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-width: 500px;
    max-height: 500px;
    aspect-ratio: 1 / 1;
    overflow: hidden;
    background: #fff;

    .loading {
      width: 4rem;
      color: #fff;
      >img {
        animation: loading 1.5s linear infinite;

        @keyframes loading {
          from {
            transform: rotateZ(0deg)
          }

          to {
            transform: rotateZ(360deg)
          }
        }
      }
    }

    .layers {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      .layer {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
      }
    }
    .styles {
      position: absolute;
      bottom: -100%;
      left: 0;
      display: flex;
      gap: 1rem;
      width: 100%;
      min-height: auto;
      padding: 1rem 2rem;
      overflow-x: auto;
      background: rgba(0, 0, 0, .8);
      transition: .2s ease-in-out;
      @media screen and (max-width: global.$sp_w) {
        min-height: calc(100px + 4rem + 5px);
        gap: 3rem;
      }
      &.checked {
        bottom: 0;
      }
      .style {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        flex-shrink: 0;
        gap: .2rem;
        align-items: center;
        cursor: pointer;
        background: #fff;
        background: transparent;
        border: .2rem solid transparent;
        &:hover {
          border: .2rem solid var(--main-color);
        }
        &[data-layer="04_water_back"],
        &[data-layer="05_hair_back"],
        &[data-layer="06_ears_back"],
        &[data-layer="11_hair_middle"],
        &[data-layer="19_water_top"] {
          display: none;
        }
        >img {
          width: 100px;
        }
        .txt {
          font-size: 10px;
          color: #fff;
          text-align: center;
          background: transparent;
        }

      }
    }

    .details {
      position: absolute;
      bottom: 0;
      left: 0;
      display: none;
      gap: 1rem;
      width: 100%;
      height: auto;
      padding: 1rem 2rem;
      // overflow-x: auto;
      background: rgba(0, 0, 0, 1);
      transition: .2s ease-in-out;

      @media screen and (max-width: global.$sp_w) {
        gap: 3rem;
      }

      &.checked {
        bottom: 0;
        display: flex;
      }
      .detail {
        display: none;
        gap: 2rem;
        align-items: center;
        cursor: pointer;
        .back-btn {
          flex-shrink: 0;
          width: 1rem;
          transition: .2s;
          &:hover {
            opacity: .5;
          }
        }
        .images {
          height: calc(100px + 2rem + 5px);
          max-width: calc(500px - 6rem);
          display: flex;
          align-items: center;
          gap: 2rem;
          overflow-x: auto;
          @media screen and (max-width: 500px) {
            max-width: calc(100vw - 6rem);
          }
        }

        .image {
          box-sizing: border-box;
          display: flex;
          flex-direction: column;
          flex-shrink: 0;
          gap: .2rem;
          align-items: center;
          cursor: pointer;
          background: #fff;
          background: transparent;
          border: .2rem solid transparent;

          &:hover {
            border: .2rem solid var(--main-color);
          }
          &.hidden {
            display: none;
          }

          >img {
            width: 100px;
          }
          .txt {
            font-size: 10px;
            color: #fff;
            text-align: center;
            background: transparent;
          }
        }

        &.checked {
          display: flex;
        }
      }
    }
  }

  .mode-btn {
    position: absolute;
    bottom: 5%;
    left: 5%;
    display: none;
    padding: .3rem .8rem;
    font-size: 1.2rem;
    color: #fff;
    background: var(--txt-color);
    transition: .2s ease-in-out;
    &:hover {
      opacity: .8;
    }
    &.show {
      display: block;
    }
  }

  .color-sort {
    position: absolute;
    bottom: 5%;
    right: 0%;
    display: flex;
    padding: .3rem .8rem;
    color: #fff;
    background: var(--txt-color);
    transition: .2s ease-in-out;
    gap: .5rem;
    opacity: 0;
    visibility: hidden;
    &.show {
      opacity: 1;
      visibility: visible;
    }
    .btn {
      background: #fff;
      width: 1.25rem;
      height: 1.25rem;
      border-radius: 50%;
      box-shadow: 0 0 1px #fff;
      transition: .2s;
      &.blue { background: #008cff; }
      &.blue-green { background: #33fec8; }
      &.green { background: #44ff46; }
      &.light-blue { background: #01ffff; }
      &.navy { background: #0032ff; }
      &.orange { background: #FF8200; }
      &.pink { background: #FF82FF; }
      &.purple { background: #8046FF; }
      &.red { background: #FF6482; }
      &.yellow { background: #FFFF50; }
      &:hover {
        opacity: .8;
      }
      &.unchecked {
        opacity: .6;
      }
    }

  }
}
