@use "global" as global;

:root {
  font-size: 14px;
  @media screen and (min-width: global.$large_display_w) {
    font-size: 1vw;
  }
  @media screen and (max-width: global.$tab_w) {
    font-size: 12px;
  }
  @media screen and (max-width: global.$sp_w) {
    font-size: 12px;
  }
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html,
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
li,
th,
td,
dl,
dt,
dd,
button {
  font-family: var(--font-en);
  font-size: 1rem;
  font-style: normal;
  font-weight: lighter;
  line-height: 1;
  line-height: 1.4;
  color: var(--txt-color);
  letter-spacing: 0.1rem;
  font-display: swap;
}

html,
body {
  -ms-touch-action: manipulation !important;
  touch-action: manipulation !important;
}
ul,
ol,
li {
  list-style: none;
}
a,
span {
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
  color: inherit;
  text-decoration: none;
  letter-spacing: inherit;
}
picture,
img {
  width: 100%;
  line-height: 0;
  vertical-align: bottom;
  border: none;
}
textarea {
  resize: none;
}
input[type="submit"] {
  -webkit-appearance: none;
  border-radius: 0;
}
button {
  padding: 0;
  cursor: pointer;
  background-color: transparent;
  border: none;
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.pc-tablet-only {
  display: block !important;

  @media screen and (max-width: global.$sp_w) {
    display: none !important;
  }
}
.sp-only {
  display: none !important;
  @media screen and (max-width: global.$sp_w) {
    display: block !important;
  }
}

// スクロールバーの扱い
* {
  &::-webkit-scrollbar {
    width: 5px;
    height: 5px;
  }

  &::-webkit-scrollbar-track {
    background-color: var(--bg-color);
    border-radius: 0;
  }

  &::-webkit-scrollbar-thumb {
    background-color: var(--main-color);
    border-radius: 0;
  }
}

